.footer{
  font-family: inherit;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  background: #2f1b5e;
  padding: 10px 20px;
  box-sizing: border-box;
  align-items: center;
  bottom: 0;
  position: fixed;
  box-shadow: 0 -2px 8px rgba(0,0,0,0.3);
  z-index: 20;
}

.footer p{
  color: white;
}

.footer a{
  color:white;
}

.footer .button{
  padding: 10px 15px;
  cursor: pointer;
  text-decoration: none;
  font-size: 16px;
  background: white;
  outline: none;
  color: #262626;
  border: 0;
  box-shadow: 0 1px 2px rgba(0,0,0,0.1);
  transition:  all ease .2s;
}

.footer button:hover{
  box-shadow: 0 2px 2px rgba(0,0,0,0.2);
  background: #efefef;
}


@media (max-width: 768px) {
  .footer{
    flex-direction: column;
    align-items: start;
  }

  .footer .button{
    padding: 5px 10px;
    font-size: 14px;
  }

  .footer a{
    margin-top: 10px;
  }
}